export class GovernmentDocumentViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#government_document_view_container',


		} );

		super( options );
	}

	init() {
		//this._super('initialize' );
		this.permission_id = 'government_document';
		this.viewId = 'GovernmentDocument';
		this.script_name = 'GovernmentDocumentView';
		this.table_name_key = 'government_document';
		this.context_menu_name = $.i18n._( 'Government Documents' );
		this.navigation_label = $.i18n._( 'Government Document' );
		this.api = TTAPI.APIGovernmentDocument;

		this.initPermission();
		this.render();
		this.buildContextMenu();

		this.initData();
	}

//	initPermission() {
//
//		super.initPermission();
//
//		if ( PermissionManager.validate( this.permission_id, 'view' ) || PermissionManager.validate( this.permission_id, 'view_child' ) ) {
//			this.show_search_tab = true;
//		} else {
//			this.show_search_tab = false;
//		}
//
//	},

	initOptions() {
		var $this = this;
		this.initDropDownOption( 'type', 'type_id' );
	}

	getCustomContextMenuModel() {
		var context_menu_model = {
			exclude: ['default'],
			include: [
				{
					label: $.i18n._( 'View' ),
					id: 'view',
					group: 'editor',
					vue_icon: 'tticon tticon-visibility_black_24dp',
					show_on_right_click: true,
				},
				{
					label: $.i18n._( 'Delete' ),
					id: 'delete_icon',
					group: 'editor',
					vue_icon: 'tticon tticon-delete_black_24dp',
					show_on_right_click: true,
				}
			]
		};

		return context_menu_model;
	}

	onViewClick() {
		this.doFormIFrameCall( { object_id: this.getGridSelectIdArray() } );

		$().TFeedback( {
			source: 'View', // viewId GovernmentDocument will be prepended in TFeedback functions.
			delay: 5000
		} );
	}

	doFormIFrameCall( post_data ) {
		var url = ServiceCaller.getURLByObjectType( 'file_download' ) + '&object_type=government_document';
		Global.APIFileDownload( null, null, post_data, url );
	}

	buildSearchFields() {

		super.buildSearchFields();
		this.search_fields = [

			new SearchField( {
				label: $.i18n._( 'Type' ),
				in_column: 1,
				field: 'type_id',
				multiple: true,
				basic_search: true,
				layout_name: 'global_option_column',
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Employee' ),
				in_column: 1,
				field: 'user_id',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Created By' ),
				in_column: 2,
				field: 'created_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Updated By' ),
				in_column: 2,
				field: 'updated_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				form_item_type: FormItemType.AWESOME_BOX
			} )
		];
	}

	setDefaultMenuViewIcon( context_btn, grid_selected_length, pId ) {
		if ( !this.viewPermissionValidate( pId ) || this.edit_only_mode ) {
			ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false )
		}

		if ( grid_selected_length >= 1 && this.viewOwnerOrChildPermissionValidate() ) {
			ContextMenuManager.disableMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, true );
		} else {
			ContextMenuManager.disableMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false );
		}
	}

}


